import React from 'react'

const Noticia = (props) => {
  const { urlToImage, url, title, description, source, content } = props.noticia;
  const imagen = content
  ? <img src={ urlToImage } alt={ title } className="card-img-top"/>
  : <p className="text-center my-5">Imagen no disponible</p>;

  return (
    <div className="col-md-6 col-12 mb-4">
      <div className="card">
        <div className="card-image">
          {imagen}
        </div>
        <div className="card-body">
          <h3 className="card-title">
            {title}
          </h3>
          <p className="card-text">{description}</p>
          <p className="card-text">{source.name}</p>
        </div>
      </div>
    </div>
  )
}

export default Noticia
